"use client"

import React from "react"

interface Metric {
  comprehension: number
  attention: number
  focus: number
  retention: number
}

interface OverviewStatsProps {
  data: Metric[]
}

export default function OverviewStats({ data }: OverviewStatsProps) {
  if (!data || data.length === 0) return <p>No metrics available</p>

  // Calculate average for each skill
  const avg = {
    comprehension:
      data.reduce((sum, d) => sum + d.comprehension, 0) / data.length,
    attention: data.reduce((sum, d) => sum + d.attention, 0) / data.length,
    focus: data.reduce((sum, d) => sum + d.focus, 0) / data.length,
    retention: data.reduce((sum, d) => sum + d.retention, 0) / data.length,
  }

  return (
    <div className="mb-6">
      {/* Top-left heading */}
      <h2 className="text-lg font-semibold text-gray-800 mb-4">
        Overview Stats
      </h2>

      {/* Grid of average stat cards */}
      <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
        <StatCard label="Comprehension" value={avg.comprehension} />
        <StatCard label="Attention" value={avg.attention} />
        <StatCard label="Focus" value={avg.focus} />
        <StatCard label="Retention" value={avg.retention} />
      </div>
    </div>
  )
}

interface StatCardProps {
  label: string
  value: number
}

function StatCard({ label, value }: StatCardProps) {
  return (
    <div className="p-4 bg-white rounded-2xl shadow text-center">
      <h3 className="text-sm text-gray-500">{label}</h3>
      <p className="text-xl font-semibold">{value.toFixed(2)}</p>
    </div>
  )
}
