"use client"

import React from "react"

interface InsightsProps {
  data: {
    assessment_score: number
    [key: string]: any
  }[]
}

export default function Insights({ data }: InsightsProps) {
  if (!data || data.length === 0) return <p>No insights available</p>

  const insightsArr = data.map(
    (s, idx) => `Student ${idx + 1} scored ${s.assessment_score.toFixed(2)}`
  )

  return (
    <ul className="list-disc list-inside">
      {insightsArr.map((i, idx) => (
        <li key={idx}>{i}</li>
      ))}
    </ul>
  )
}
